export default {
name: "",
components: {

},
props: {},
data () {
  return {
	  trailerList: [],
	  page: 1,
	  pageSize: 30,
	  keywords: "",
	  // 控制提示用户已全部加载的显示与隐藏
	  lowerOpen: true
    }
  },
  onLoad () {
  	this.getTrailerList();
  },
  methods: {
	// 获取预告片列表
	getTrailerList() {
		uni.request({
			url: `${this.$api}/search/list?qq=122212489&keywords=${this.keywords}&page=${this.page}&pageSize=${this.pageSize}`,
			method: 'POST',
			data: {},
			success: res => {
				if(res.data.data.rows.length === 0) {
					// 提示用户加载完毕
					this.lowerOpen = false
				}else {
					if(this.page > 0) {
						// 当前页大于0时 原来的数据和当前的数据合并一起展示
						this.trailerList = this.trailerList.concat(res.data.data.rows)
					}else {
						// 当前页等于0时的数据 
						this.trailerList = res.data.data.rows
					}
				}
			},
		});
	},
	// 上拉刷新
	lower() {
		if(this.lowerOpen) {
			this.page += 1
			this.getTrailerList()
		}
	},
	// 获取关键字
	getKeyWords(e) {
		this.keywords = e.detail.value
		this.page = 0
	},
	// 搜索
	search() {
		this.page = 0
		this.getTrailerList()
	},
	// 去详情页面
	goDetail(id) {
		uni.navigateTo({
			url: `/pages/detail/detail?id=${id}`,
		});
	}
  }
}
